  async calculateProjectionChart(field, selectedItems, startDate = null) {
    if (!globalState.drawChartPlot?.data || !globalState.drawGrowthRatePlot?.data)
      return

    await initProvinces()


    const getMax = (data) => {
        const maxYs = data[0].map((a,i) => 
                        i == 0 ? 0 : max2(data.map((r,j) => j == 0 ? 0 : r[i]).filter(_.isFinite)).max)
        const maxY = Math.max(...maxYs)
        return maxY
    }

    const getAvg = (data) => {
        const avg = _.range(0, data[1].length - 1).map(a => null)
        for (let j = 1; j < data[1].length; j++) {
         let averaged = 0
         for(let i = 1; i < data.length; i++) {
               if (!_.isFinite(data[i][j]))
                 continue
               avg[j] += data[i][j]
               averaged++
         }
         avg[j] /= averaged
        }
        return average(avg.filter(_.isFinite))
    }

    const getNearestIdx = (data, y) => {
        let md = +Infinity
        let mi = -1
        for (let j = 1; j < data[1].length; j++) {
         for(let i = 1; i < data.length; i++) {
             if (!_.isFinite(data[i][j]))
                 continue
             if (Math.abs(data[i][j]-y) < md) {
                 md = Math.abs(data[i][j]-y)
                 mi = i
             }
         }
        }
        return {i: mi, y: md}
    }

    let startIdx = -1
    if (!startDate) {
        const D = globalState.drawGrowthRatePlot.data
        const ni = getNearestIdx(D, getAvg(D))
        if (ni.i >= 0) {
            startDate = D[ni.i][0]
            startIdx = ni.i
        }
    }
    if (startDate && startIdx < 0) {
        startIdx = globalState.drawGrowthRatePlot.data.findIndex((r,i) => i > 0 && r[0].getTime() == startDate.getTime())
    }

    this.setState({
      projPlotStart: {
        gr: startIdx >= 0 ? globalState.drawGrowthRatePlot.data[startIdx].slice(1, -1) : [],
        date: startDate,
        series: globalState.drawGrowthRatePlot.data[0].slice(1, -1).map(c => c.label)
      }
    })

    const plot = await projectGeneralPlot(selectedItems.length, globalState.drawChartPlot, globalState.drawGrowthRatePlot, startDate)


    plot.gviz = GoogleCharts.api.visualization.arrayToDataTable(plot.data);

    const dataMax = getMax(globalState.drawChartPlot.data)
    const projDataMax = getMax(plot.data)

    const safeMax = projDataMax > dataMax ? Math.min(4*dataMax, projDataMax) : dataMax

    return {plot, safeMax}
  }

  /* Important: this function must be AFTER drawChart() and drawGrowthRateChart() */
  async drawProjectionChart(field, selectedItems, startDate = null) {

    const {plot, safeMax} = await this.calculateProjectionChart(field, selectedItems, startDate)

    const options = {
      legendFormatter: this.legendFormatter,
      // labelsSeparateLines: true,
      strokeWidth: 3,
      legend: 'always',
      labelsDiv: 'projLegendDiv',
      colorValue: 1,
      series: plot.options,
      valueRange: [0, safeMax],
    }

    if (globalState.drawProjectionChartGraph)
      globalState.drawProjectionChartGraph.destroy()

    globalState.drawProjectionChartGraph = new Dygraph(
      document.getElementById("projPlotDiv"),
      plot.gviz,
      options
    )
    globalState.drawProjectionChartGraph.resize()

    const g = globalState.drawProjectionChartGraph
    g.ready(() => {
          g.updateOptions({ 
              pointClickRadius: 50,
              pointClickCallback: async (ev, point) => {
                const i = point.idx
                const data = globalState.drawChartPlot.data
                const startDate = data[i+1][0]
                const {plot, safeMax} = await this.calculateProjectionChart(field, selectedItems, startDate)
                g.updateOptions({ 
                    file: plot.gviz,
                    valueRange: [0, safeMax]
                })
              }
          })
    })


  }

  async drawTamponiChart(field, selectedItems) {

    //await initProvinces()
    //await initRegions() done internally by testsPlot()

    const plotT = await testsPlot(field, selectedItems, 0)
    const plotP = _.cloneDeep(plotT)

    for (let i = 1; i < plotT.data.length; i++) {
      for (let j = 1; 2 * j < plotT.data[i].length; j++) {
        plotT.data[i][2 * j] = null
        plotP.data[i][2 * (j - 1) + 1] = null
      }
    }

    const yPBounds = smallYBounds(plotP.data.slice(1), 5)

    plotT.gviz = GoogleCharts.api.visualization.arrayToDataTable(plotT.data);

    plotP.gviz = GoogleCharts.api.visualization.arrayToDataTable(plotP.data);

    const optionsT = {
      legendFormatter: this.legendFormatter,
      // labelsSeparateLines: true,
      strokeWidth: 3,
      legend: 'always',
      labelsDiv: 'tampTLegendDiv',
      colorValue: 1,
      series: plotT.options,
    }

    globalState.drawTampTChartGraph = new Dygraph(
      document.getElementById("tampTPlotDiv"),
      plotT.gviz,
      optionsT
    )
    globalState.drawTampTChartGraph.resize()

    const optionsP = _.clone(optionsT)
    optionsP.labelsDiv = 'tampPLegendDiv'
    optionsP.series = _.clone(optionsT.series)
    optionsP.valueRange = yPBounds

    globalState.drawTampPChartGraph = new Dygraph(
      document.getElementById("tampPPlotDiv"),
      plotP.gviz,
      optionsP
    )
    globalState.drawTampPChartGraph.resize()

  }

  async drawMaxVel(field) {
    await initProvinces();

    // ploty is children of layout component

    const plot = await maxVelocityPlot(field)

    plot.options = _.mapValues(plot.options, (subObj) => {
      return _.mapValues(subObj, (v, k) => {
        if (k === 'plotter' && v === "barChartPlotter")
          return this.barChartPlotter.bind(this)
        else
          return v
      })
    })

    const revPlot = plot.data.slice().reverse()
    const lastIdx = revPlot.findIndex(p => p[2] > 0)
    if (lastIdx > 0) {
      this.setState({
        maxVelLastPercRow: revPlot[lastIdx]
      })
    }
    this.setState({
      maxVelNumberOfProvinces: plot.data.slice(1).reduce((p, c) => p + c[1], 0),
      maxVelLastNullPercRows: plot.data.slice(1).filter(p => !_.isNumber(p[2]))
    })

    plot.gviz = GoogleCharts.api.visualization.arrayToDataTable(plot.data);

    const options = {
      legendFormatter: this.legendFormatter,
      // labelsSeparateLines: true,
      strokeWidth: 3,
      legend: 'always',
      labelsDiv: 'maxVelLegendDiv',
      colorValue: 1,
      series: plot.options,

    }
    globalState.drawMaxVelGraph = new Dygraph(
      document.getElementById("maxVelPlotDiv"),
      plot.gviz,
      options
    )
    globalState.drawMaxVelGraph.resize()


  }






  async  _draw(field, selectedItems) {
    switch (this.state.value) {
      case 0: {
        await this.drawChart(field, selectedItems);
      }
        break;
      case 1: await this.drawMaxVel(field, selectedItems);
        break;
      case 2: await this.drawGrowthRate(field, selectedItems);
        break;
      case 3:

        // projectionChart needs up-to-date base chart and grow-rate-chart
        await this.drawChart(field, selectedItems, false);
        await this.drawGrowthRate(field, selectedItems, false);

        await this.drawProjectionChart(field, selectedItems)

        break;

      //case 4: // r0

      case 4:
        await this.drawTamponiChart(field, selectedItems)
        break

      default:
    }
  }

  handleChange(event, value) {
    const { field } = this.props;
    const selectedItems = field === "denominazione_regione" ?
      this.props.regionValues :
      this.props.provinceValues;

    this.setState({ value: value }, () => {
      globalState.drawChartDrawing = false;
      this._draw(field, selectedItems)
    })
  }

  getLabel() {
    const { value } = this.state;
    switch (value) {
      default: return 'Approfondimento'
    }
  }

